<template>
  <div class='investor-dashboard-desktop-balance-chart'>
    <inline-navigation
      :links='links'
      :opened='opened'
      @onOpen='(id) => {opened = id}'
      class='investor-dashboard-desktop-balance-chart__nav'/>
    <transition
      name="custom-classes-transition"
      enter-active-class="animated dur02 fadeIn"
      leave-active-class="animated dur02 fadeOut"
      appear
      mode="out-in"
    >
      <chart-wrapper
        class='investor-dashboard-desktop-balance-chart__chart'
        label='Chart Balance'
        chart-type='line'
        :options='chartOptions'
        :height='120'
        v-if='opened === 1'
        key='my'/>
      <chart-wrapper
        class='investor-dashboard-desktop-balance-chart__chart'
        label='Chart Balance'
        chart-type='line'
        :options='chartOptions'
        :height='120'
        v-else
        key='all'/>
    </transition>
  </div>
</template>
<script>
import ChartWrapper from '@/components/common/chart/ChartWrapper.vue';
import moment from 'moment-timezone';

export default {
  name: 'InvestorDashboardDesktopBalanceChart',
  components: {
    ChartWrapper,
  },
  data() {
    return {
      links: ['Strategy chart', 'Strategy balance'],
      opened: 1,
      chartOptions: {
        responsive: [{
            breakpoint: 768,
            options: {
              chart: {
                height: 120,
              },
            }
          }],
          chart: {
            fontFamily: '"sfpro", -apple-system, BlinkMacSystemFont, sans-serif',
            toolbar: { show: false, },
            type: 'line',
          },
          colors: ['#2550d9'],
          fill: {
            type: ['solid'],
          },
          stroke: { curve: 'straight', width: 2, },  
          legend: { show: false, },
          
          dataLabels: { enabled: false },
          markers: { size: 0 },
          row: { colors: undefined, opacity: 0 },
          grid: {
            borderColor: '#313438',
            xaxis: { lines: { show: true, }, },
            yaxis: { lines: { show: false, }, }
          },
          xaxis: {
            type: 'datetime',
            labels: { show: false, },
            axisBorder: { show: false, },
            axisTicks: { show: false, },
          },
          yaxis: {
            labels: { show: false, },
            axisBorder: { show: false, },
            axisTicks: { show: false, },
          },
          tooltip: {
            shared: true,
            theme: 'dark',
            intersect: false,
            custom({series, seriesIndex, dataPointIndex, w}) {
              return `<div class="graph-tooltip">
                  <span class="graph-tooltip__name">${w.config.series[seriesIndex].name}</span>
                  <span class="graph-tooltip__value"
                  >$${series[seriesIndex][dataPointIndex]}</span> 
                  <span class="graph-tooltip__date"
                    >${moment.unix(w.config.series[seriesIndex].data[dataPointIndex][0]).format('DD MMMM, YYYY')}
                  </span> 
                </div>`
            },
            y: {
              formatter(y) {
                return (typeof y !== "undefined") ? (y.toFixed(0) + " points") : y;
              },
            },
            x: {
              formatter(x) {
                return (typeof x !== "undefined") ? (moment.unix(x).format('DD.MM.YY HH:mm')) : x;
              },
            },
          },
      }
    }
  }
}
</script>
<style lang="scss">
$styles: investor-dashboard-desktop-balance-chart;

.#{$styles} {
  border: 1px solid $N10;
  padding: 20px 28px;
  &__nav {
    margin-bottom: 16px; 
  }
  &__chart {
    .saas-chart__box {
      border: none;
      padding: 0;
    }
    padding: 0;
    border: none;
    margin: 0;
  }
}
</style>
