<template>
<transition
      v-on:enter="enter"
      v-on:leave="leave">
  <div class="investor-dashboard-history-item-drop">
    <div class="investor-dashboard-history-item-drop__row">
      <div class="investor-dashboard-history-item-drop__item">
        <p class="investor-dashboard-history-item-drop__label">Currency</p>
        <p class="investor-dashboard-history-item-drop__value-row">
          <cryptoicon
            class="investor-dashboard-history-item-drop__value-icon"
            symbol="btc"
            size="16"/> 
          <span class="investor-dashboard-history-item-drop__value">Bitcoin</span>
        </p>
      </div>
      <div class="investor-dashboard-history-item-drop__item" v-if='isDeposit'>
        <p class="investor-dashboard-history-item-drop__label">Recieved on</p>
        <p class="investor-dashboard-history-item-drop__value-row">
          <icon-wallet class="investor-dashboard-history-item-drop__value-icon"/>
          <span class="investor-dashboard-history-item-drop__value">Bitcoin</span>
        </p>
      </div>
      <div class="investor-dashboard-history-item-drop__item" v-else>
        <p class="investor-dashboard-history-item-drop__label">Payout address</p>
        <p class="investor-dashboard-history-item-drop__value-row">
          <icon-wallet class="investor-dashboard-history-item-drop__value-icon"/>
          <span class="investor-dashboard-history-item-drop__value">Bitcoin</span>
        </p>
      </div>
    </div>
    <div class="investor-dashboard-history-item-drop__row">
      <div class="investor-dashboard-history-item-drop__item">
       <p class="investor-dashboard-history-item-drop__label">Status</p>
       <p
        class="investor-dashboard-history-item-drop__value"
        :class='{"rejected": status === "rejected", "successful": status === "successful"}'
        >Successful</p>
      </div>
    </div>
  </div>
  </transition>
</template>
<script>
import IconWallet from '@/components/common/icons/IconWallet.vue';
import 'velocity-animate';

export default {
  name: 'InvestorDashboardHistoryItemDrop',
  props: {
    isDeposit: {
      type: Boolean,
      default: false,
    },
    status: {
      type: String,
      default: 'successful',
    }
  },
  components: {
    IconWallet,
  },
  methods: {
    enter(el, done) {
      Velocity(el, "slideDown", { duration: 200 })
    },
    leave(el, done) {
      Velocity(el, "slideUp", { duration: 200 })
    }
  },
}
</script>

<style lang="scss">
$styles: investor-dashboard-history-item-drop;

.#{$styles} {
  .rejected {
    color: $Y1;
  }
  .successful {
    color: $G1;
  }
  @extend %mobile-list-drop;
  &__row {
    @extend %mobile-list-drop-row;
    margin-bottom: 18px;
  }
  &__item {
    @extend %mobile-list-drop-item;
    min-width: 100px;
  }
  &__label {
    @extend %mobile-list-drop-label;
  }
  &__value {
    @include text($H200, 400);
  }
  &__value-row {
    @include flex-row(flex-start, center);
  }
  &__value-icon {
    $size: 16px;
    width: $size;
    height: $size;
    margin-right: 6px;
    fill: $B4;
  }
  &__crypto-icon {
    margin-right: 6px;
  }
}
</style>

