<template>
  <div class="dashboard-home-chart">
    <header class="dashboard-home-chart__header">
      <h2 class="dashboard-home-chart__title">Total funds balance</h2>
      <chart-filter-button :rotate='filter' @toggle='filter = !filter'/>
    </header>
    <chart-filter-mobile :visible='filter' @updateChart='updateChart'/>
    <div class="dashboard-home-chart__box-wrapper">
      <div class="dashboard-home-chart__box">
        <div class="dashboard-home-chart__row">
          <chart-label
            :val='tweenedNumber'
            class="dashboard-home-chart__label">
            <span class='desktop-none'>All funds</span>
            <span class='tablet-inline'>Total funds balance</span>
          </chart-label>
          <chart-filter-desktop
            @updateChart='updateChart'/>
        </div>
        <apexchart height=150 :options="Chart.options" :series="Chart.series" />
      </div>
    </div>
  </div>
</template>
<script>
import ChartFilterButton from '@/components/common/chart/ChartFilterButton.vue';
import ChartFilterMobile from '@/components/common/chart/ChartFilterMobile.vue';
import ChartFilterDesktop from '@/components/common/chart/ChartFilterDesktop.vue';
import ChartLabel from '@/components/common/chart/ChartLabel.vue';
import '@/scss/components/graph-tooltip.scss';
import VueApexCharts from 'vue-apexcharts';
import moment from 'moment-timezone';
import saasChart from '@/mixins/chart';

export default {
  name: 'DashboardHomeChart',
  mixins: [saasChart],
  components: {
    ChartFilterButton,
    ChartFilterMobile,
    ChartFilterDesktop,
    apexchart: VueApexCharts,
    ChartLabel,
  },
  data() {
    return {
      filter: false,
      allFunds: 32400.934802,
      tweenedNumber: 0,
      Chart: {
        series: [
          {
            name: 'Total funds balance',
            type: 'area',
            data: [
              [1333922402, 33.22],
              [1333922403, 20.61],
              [1333922404, 60.61],
              [1333922405, 53.61],
              [1333922407, 43.61],
              [1333922409, 60.61],
              [1333922411, 62.61],
              [1333922412, 61.61],
              [1333922415, 35.61],
              [1333922416, 45.61],
            ],
          },
        ],
        options: {
          responsive: [{
            breakpoint: 768,
            options: {
              chart: {
                height: 120,
              },
            }
          }],
          chart: {
            fontFamily: '"sfpro", -apple-system, BlinkMacSystemFont, sans-serif',
            toolbar: { show: false, },
            type: 'line',
          },
          colors: ['#2550d9', '#ffab00', '#546E7A', '#E91E63', '#00b853'],
          fill: {
            type: ['gradient'],
            gradient: {
              shade: 'dark',
              type: "vertical",
              shadeIntensity: 0.5,
              gradientToColors: undefined,
              inverseColors: false,
              opacityFrom: 0.6,
              opacityTo: 0.4,
              stops: [0, 100],
              colorStops: []
            },
          },
          stroke: { curve: 'straight', width: 2, },  
          legend: { show: false, },
          
          dataLabels: { enabled: false },
          markers: { size: 0 },
          row: { colors: undefined, opacity: 0 },
          grid: {
            borderColor: '#313438',
            xaxis: { lines: { show: true, }, },
            yaxis: { lines: { show: false, }, }
          },
          xaxis: {
            type: 'datetime',
            labels: { show: false, },
            axisBorder: { show: false, },
            axisTicks: { show: false, },
          },
          yaxis: {
            labels: { show: false, },
            axisBorder: { show: false, },
            axisTicks: { show: false, },
          },
          tooltip: {
            shared: true,
            theme: 'dark',
            intersect: false,
            custom({series, seriesIndex, dataPointIndex, w}) {
              return `<div class="graph-tooltip">
                  <span class="graph-tooltip__name">${w.config.series[seriesIndex].name}</span>
                  <span class="graph-tooltip__value"
                  >$${series[seriesIndex][dataPointIndex]}</span> 
                  <span class="graph-tooltip__date"
                    >${moment.unix(w.config.series[seriesIndex].data[dataPointIndex][0]).format('DD MMMM, YYYY')}
                  </span> 
                </div>`
            },
            y: {
              formatter(y) {
                return (typeof y !== "undefined") ? (y.toFixed(0) + " points") : y;
              },
            },
            x: {
              formatter(x) {
                return (typeof x !== "undefined") ? (moment.unix(x).format('DD.MM.YY HH:mm')) : x;
              },
            },
          },
        },
      },
    };
  },
  methods: {
    animateNumber(value) {
      TweenLite.to(this.$data, 1, { tweenedNumber: value });
    }
  },
  watch: {
    allFunds(value) {
      this.animateNumber(value);
    },
  },
  mounted() {
    setTimeout(() => this.animateNumber(this.allFunds), 1000);
  },
}
</script>
<style lang="scss">
$dashboard-home-chart: dashboard-home-chart;

.#{$dashboard-home-chart} {
  @include saas-chart {
    width: 100%;
  }
  &__title {
    @extend %saas-chart-title;
  }
  &__box {
    @extend %card-desktop;
    box-shadow: none;
    @include media {
      background-color: $N12;
      border-color: $N10;
    }
  }
  &__box-wrapper {
    @include saas-chart-box-wrapper {
      @include media {
        padding: 0;
      }
    }
  }
  &__header {
    @include saas-chart-header {
      @include media {
        display: none;
      }
    }
  }
  &__label {
    @include media {
      margin-right: 54px;
    }
  }
  &__row {
    @include flex-row(flex-start, center);
  }
}
</style>
