import { exchanges } from '@/helpers/exchanges';

const state = {
  myApi: [],
  api: [],
};

const mutations = {
  updateExhanges(state, data) {
    state.myApi = [...data]; 
  },
  setApi(state, data) {
    state.api = [...data]; 
  },
  addApi(state, api) {
    state.myApi.unshift(api);
  },
  removeApi(state, key) {
    const idx = state.myApi.findIndex((item) => item.apiKey === key);
    state.myApi.splice(idx, 1);
  },
};

const getters = {
  isApi: (state) => {
    return !!state.myApi.length;
  },
  getImages: (state) => {
    const images = {};
    state.api.forEach((element) => {
      images[element.name] = element.image; 
    });
    return images;
  },
};

// TODO: Promis functions must be replaced by Http requests
const actions = {
  downloadApi({ commit }) {
    commit('setApi', exchanges);
  },
  addExchange({ dispatch, commit }, data) {
    return new Promise((resolve, reject) => {
      const newData = Object.assign({}, data);
      setTimeout(() => {
        commit('addApi', newData);
        setTimeout(() => {
          dispatch('messages/success', 'Exchange asset added successfully', {root: true});
          resolve();
        }, 500);
      }, 500);
    })
  },
  deleteExchange({ dispatch, commit }, key) {
    dispatch('messages/loading', 'Wait a second!', {root: true});
    setTimeout(() => {
      commit('removeApi', key);
      dispatch('messages/success', 'Exchange asset removed successfully', {root: true});
    }, 1000) 
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
