<template>
<div class="flag-message-loading__wrapper">
  <div
    class="flag-message-loading">
    <half-circle-spinner
      :animation-duration="800"
      :size="20"
      :color="'#484c52'"/>
    <span class="flag-message-loading__text">
      {{ message }}
    </span>
  </div>
</div>
</template>
<script>
import { HalfCircleSpinner } from 'epic-spinners'
import { mapState } from 'vuex';

export default {
  name: 'MessageLoading',
  components: {
    HalfCircleSpinner,
  },
  computed: {
    ...mapState({
      message: state => state.messages.message.loading,
    })
  }
};
</script>
<style lang="scss">
@import '~@/scss/components/flag-message';
.flag-message-loading {
  @extend %flag-message;
  background-color: $N0;
  border-radius: 0;
  @media screen and (min-width: $screen-tablet) {
    border-radius: 2px;
  }
  &__wrapper {
    @extend %flag-message-wrapper;
  }
  &__image {
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    fill: $N9;
    margin-right: 8px;
  }
  &__text {
    margin-left: 12px;
    @extend %flag-message-text;
    color: $N9;
  }
}
</style>
