<template>
  <main class="investor-dashboard">
    <invest v-if='show.invest' label='Top 10' @close='show.invest = false'/>
    <request v-if='show.request' @close='show.request = false'/>
    <investor-dashboard-details
      :layer='show.invest || show.request'
      @showInvest='show.invest = true'
      @showRequest='show.request = true'
      v-if='show.details'
      @close='show.details = false'/>
    <template v-else>
      <header class="investor-dashboard__header">
        <h1 class="investor-header__title">Dashboard</h1>
      </header>
      <dashboard-home-chart/>
      <h3 class='investor-dashboard__subtitle'>All strategies</h3>
      <div class="investor-dashboard__view">
        <investor-all-strategies 
          class="investor-dashboard__view-box"
          @open='openDetails'/>
      </div>
    </template>
  </main>

</template>
<script>
import DashboardHomeChart from '@/components/investor/dashboard/home/DashboardHomeChart.vue';
import InvestorAllStrategies from '@/components/investor/dashboard/all/InvestorAllStrategies.vue';
import InvestorActiveStrategies from '@/components/investor/dashboard/active/InvestorActiveStrategies.vue';
import DashboardEmpty from '@/components/investor/dashboard/home/DashboardEmpty.vue';
import InvestorDashboardDetails from '@/components/investor/dashboard/details/InvestorDashboardDetails.vue';
import Invest from '@/components/investor/dashboard/components/Invest.vue';
import Request from '@/components/investor/dashboard/components/Request.vue';

export default {
  name: 'InvestorDashboard',
  components: {
    DashboardHomeChart,
    InvestorAllStrategies,
    InvestorActiveStrategies,
    DashboardEmpty,
    InvestorDashboardDetails,
    Invest,
    Request,
  },
  data() {
    return {
      links: [
        'Active strategies',
        "All strategies",
      ],
      opened: 1,
      detailsId: null,
      show: {
        details: false,
        invest: false,
        request: false,
      },
    }
  },
  methods: {
    openDetails(id) {
      this.detailsId = id;
      this.show.details = true;
    }
  }
};
</script>
<style lang="scss">
@import '~@/scss/components/investor';

$investor-dashboard: investor-dashboard;

.#{$investor-dashboard} {
  @extend %investor-main;
  padding-top: 24px;
  &__header {
    margin-bottom: 24px;
    @include isDesktop();
  }
  &__title {
    @extend %investor-title;
  }
  &__subtitle {
    font-size: $H700;
    font-weight: 500;
  }
  &__view {
    flex: 1 1;
    @include flex-col(flex-start, center);
    width: 100%;
    padding: 28px;
    @include media {
      padding: 28px 0;
    }
  }
  &__view-box {
    flex: 1 1;
    @include flex-col(flex-start, center);
    @include media {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
}
</style>
