<template>
  <div class="saas-chart">
    <header class="saas-chart__header">
      <h2 class="saas-chart__title">{{ label }}</h2>
      <chart-filter-button :rotate='filter' @toggle='filter = !filter'/>
    </header>
    <chart-filter-mobile :visible='filter' @updateChart='updateData'/>
    <div class="saas-chart__box-wrapper">
      <div class="saas-chart__box">
        <div class="saas-chart__row">
          <chart-filter-desktop
            @updateChart='updateData'/>
        </div>
        <apexchart :height='height' :type='chartType' :options="Chart.options" :series="Chart.series" />
      </div>
    </div>
  </div>
</template>
<script>
import ChartFilterButton from '@/components/common/chart/ChartFilterButton.vue';
import ChartFilterMobile from '@/components/common/chart/ChartFilterMobile.vue';
import ChartFilterDesktop from '@/components/common/chart/ChartFilterDesktop.vue';
import ChartLabel from '@/components/common/chart/ChartLabel.vue';
import '@/scss/components/graph-tooltip.scss';
import VueApexCharts from 'vue-apexcharts';
import moment from 'moment-timezone';
import saasChart from '@/mixins/chart';

export default {
  name: 'DashboardHomeChart',
  mixins: [saasChart],
  props: {
    height: Number,
    label: String,
    chartType: {
      type: String,
      default: 'line',
    },
    chartData: {
      type: Array,
      default() {
        return [];
      }
    },
    options: {
      type: Object,
      default() {
        return {
          responsive: [{
            breakpoint: 768,
            options: {
              chart: {
                height: 120,
              },
            }
          }],
          chart: {
            fontFamily: '"sfpro", -apple-system, BlinkMacSystemFont, sans-serif',
            toolbar: { show: false, },
          },
          colors: ['#2550d9'],
          fill: {
            type: ['gradient'],
            gradient: {
              shade: 'dark',
              type: "vertical",
              shadeIntensity: 0.5,
              gradientToColors: undefined,
              inverseColors: false,
              opacityFrom: 0.6,
              opacityTo: 0.4,
              stops: [0, 100],
              colorStops: []
            },
          },
          stroke: { curve: 'straight', width: 1, },  
          legend: { show: false, },
          
          dataLabels: { enabled: false },
          markers: { size: 0 },
          row: { colors: undefined, opacity: 0 },
          grid: {
            borderColor: '#313438',
            xaxis: { lines: { show: true, }, },
            yaxis: { lines: { show: false, }, }
          },
          xaxis: {
            type: 'datetime',
            labels: { show: false, },
            axisBorder: { show: false, },
            axisTicks: { show: false, },
          },
          yaxis: {
            labels: { show: false, },
            axisBorder: { show: false, },
            axisTicks: { show: false, },
          },
          tooltip: {
            shared: true,
            theme: 'dark',
            intersect: false,
            custom({series, seriesIndex, dataPointIndex, w}) {
              return `<div class="graph-tooltip">
                  <span class="graph-tooltip__name">${w.config.series[seriesIndex].name}</span>
                  <span class="graph-tooltip__value"
                  >$${series[seriesIndex][dataPointIndex]}</span> 
                  <span class="graph-tooltip__date"
                    >${moment.unix(w.config.series[seriesIndex].data[dataPointIndex][0]).format('DD MMMM, YYYY')}
                  </span> 
                </div>`
            },
            y: {
              formatter(y) {
                return (typeof y !== "undefined") ? (y.toFixed(0) + " points") : y;
              },
            },
            x: {
              formatter(x) {
                return (typeof x !== "undefined") ? (moment.unix(x).format('DD.MM.YY HH:mm')) : x;
              },
            },
          },
        }
      }
    },
  },
  components: {
    ChartFilterButton,
    ChartFilterMobile,
    ChartFilterDesktop,
    apexchart: VueApexCharts,
  },
  data() {
    return {
      filter: false,
      Chart: {
        series: [
          {
            name: this.label,
            data: [],
          },
        ],
        options: this.options,
      },
    };
  },
  methods: {
    updateData(timeline) {
      switch (timeline) {
        case 'week':
          this.Chart.options = Object.assign({}, this.Chart.options, {
            xaxis: {
              min: moment().subtract(1, 'week').unix(),
              max: moment().unix(),
            }
          })
          break;
        case 'month':
          this.Chart.options = {
            xaxis: {
              min: moment().subtract(1, 'month').unix(),
              max: moment().unix(),
            }
          }
          break;
        case 'year':
          this.Chart.options = Object.assign({}, this.Chart.options, {
            xaxis: {
              min: moment().subtract(1, 'year').unix(),
              max: moment().unix(),
            }
          })
          break;
        case 'all':
          this.Chart.options = {
            xaxis: {
              min: undefined,
              max: undefined,
            }
          }
          break;
        default:
      }
    },
    updateChart() {
      const data = this.chartData.map(([date, value]) => {
        return [date, value.toFixed(4)]
      })
      this.$set(this.Chart.series, 0, {name: this.label, data})
    },
  },
  mounted() {
    this.updateChart();
  },
  watch: {
    chartData() {
      this.updateChart();
    },
  }
}
</script>
<style lang="scss">
$dashboard-home-chart: saas-chart;

.#{$dashboard-home-chart} {
  @include saas-chart {
    width: 100%;
  }
  &__title {
    @extend %saas-chart-title;
  }
  &__box {
    @extend %card-desktop;
    box-shadow: none;
    @include media {
      background-color: $N12;
      border-color: $N10;
    }
  }
  &__box-wrapper {
    @include saas-chart-box-wrapper {
      @include media {
        padding: 0;
      }
    }
  }
  &__header {
    @include saas-chart-header {
      @include media {
        display: none;
      }
    }
  }
  &__label {
    @include media {
      margin-right: 54px;
    }
  }
  &__row {
    @include flex-row(flex-start, center);
  }
}
</style>
