<template>
  <div class="investor-dashboard-pie-chart">
    <div class="investor-dashboard-pie-chart__chart-wrapper">
      <div class="investor-dashboard-pie-chart__pie"></div>
      <apexchart type=donut height=180 width='290' :options="chartOptions" :series="series" />
    </div>
    <ul class="investor-dashboard-pie-chart__list">
      <li class="investor-dashboard-pie-chart__item" v-for='n in 5' :key='n'>
        <span class="investor-dashboard-pie-chart__label">$100,500</span>
        <span class="investor-dashboard-pie-chart__value">50%</span>
      </li>
    </ul>
  </div>
</template>
<script>
import VueApexCharts from 'vue-apexcharts';

export default {
  name: 'InvestorDashboardPieChart',
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      series: [44, 55, 41, 17, 15],
      chartOptions: {
        title: {
            text: 'Assets',
            align: 'right',
            margin: 0,
            offsetX: -18,
            offsetY: 0,
            floating: true,
            style: {
              fontSize:  '20px',
              color:  '#FFFFFF',
            },
        },
        labels: ['Apple', 'Mango', 'Orange', 'Watermelon', 'asd'],
        legend: {
          position: 'right',
          fontSize: '12px',
          offsetX: -35,
          offsetY: 15,
          labels: {
            colors: '#9399A3',
            useSeriesColors: false
          },
          markers: {
              width: 8,
              height: 8,
          },
          itemMargin: {
            horizontal: 2,
            vertical: 0
          },
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          show: false,
          curve: 'smooth',
          lineCap: 'butt',
          colors: undefined,
          width: 2,
          dashArray: 0,      
        },
        plotOptions: {
          pie: {
            size: 80,
            donut: {
              size: 90,
              labels: {
                show: true,
                name: {
                  color: '#818892',
                  fontSize: '11px',
                  offsetY: 10,
                },
                value: {
                  color: '#FFFFFF',
                  fontSize: '18px',
                  offsetY: -32,
                },
                total: {
                  show: true,
                  label: 'Available for payout',
                  formatter(w) {
                    return `$${w.globals.seriesTotals.reduce((a, b) => {
                      return a + b
                    }, 0)}`
                  }
                },
              },
            },
          },
          
        },
      },
    }
  }
}
</script>

<style lang="scss">
$styles: investor-dashboard-pie-chart;

.#{$styles} {
  border: 1px solid $N10;
  padding: 20px 28px;
  @include flex-row(space-between, flex-start);
  &__chart-wrapper {
    position: relative;
  }
  &__pie {
    border: 1px solid $N9;
    background-color: #191E25;
    background: #191E25; /* Old browsers */
    background: -moz-radial-gradient(center, ellipse cover, #191E25 35%, #29303a 70%); /* FF3.6-15 */
    background: -webkit-radial-gradient(center, ellipse cover, #191E25 35%, #29303a 70%); /* Chrome10-25,Safari5.1-6 */
    background: radial-gradient(ellipse at center, #191E25 35%, #29303a 70%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    border-radius: 50%;
    width: 190px;
    height: 190px;
    position: absolute;
    left: -6px;
    top: -2px;
  }
  &__list {
    list-style: none;
    padding-top: 40px;
  }
  &__item {
    margin-bottom: 9px;
  }
  &__label {
    @include text($H200, 500);
    margin-right: 7px;
  }
  &__value {
    @include text($H100, 600, $G1);
  }
  .apexcharts-datalabel-label {
    font-weight: 500;
  }
  .apexcharts-datalabel-value {
    font-weight: 600;
  }
  .apexcharts-legend-marker {
    margin-right: 12px;
  }
  .apexcharts-legend-text {
    font-weight: 600;
  }
}
</style>
