<template>
<div
  class="investor-dashboard-history-item">
  <div class="investor-dashboard-history-item__card">
    <ul class="investor-dashboard-history-item__list">
      <li class="investor-dashboard-history-item__item">
        <span class="investor-dashboard-history-item__date">24/01/2019</span>
        <span class="investor-dashboard-history-item__time">11:23 AM</span>
      </li>
      <li class="investor-dashboard-history-item__item">
        <span class="investor-dashboard-history-item__value">76 078 $</span>
        <button
          class="investor-dashboard-history-item__icon-arrow-button"
          @click.stop.prevent='openDrop'>
        <icon-arrow-down
          class='investor-dashboard-history-item__icon-arrow'
          :class='{"rotate-180": dropMenu}'/>
        </button>
      </li>
    </ul>
  </div>
  <investor-dashboard-history-item-drop :is-deposit='isDeposit' v-if='dropMenu'/>
</div>
</template>
<script>
import IconArrowDown from '@/components/common/icons/IconArrowDown.vue';
import InvestorDashboardHistoryItemDrop from '@/components/investor/dashboard/details/InvestorDashboardHistoryItemDrop.vue';

export default {
  name: 'InvestorDashboardHistoryItem',
  props: {
    isDeposit: {
      type: Boolean,
      default: false,
    },
    id: [String, Number],
  },
  components: {
    IconArrowDown,
    InvestorDashboardHistoryItemDrop,
  },
  data() {
    return {
      dropMenu: false,
    };
  },
  methods: {
    closeDrop(id) {
      if (id !== this.id) {
        this.dropMenu = false;
      }
    },
    openDrop() {
      this.dropMenu = !this.dropMenu;
      this.$emit('closeDrop', this.id);
    }
  },
};
</script>
<style lang="scss">
$slyles: investor-dashboard-history-item;

.#{$slyles} {
  &__card {
    @extend %mobile-list-card;
  }
  &__list {
    @extend %mobile-list-sublist;
  }
  &__item {
    @extend %mobile-list-item;
  }
  &__icon-arrow-button {
    @extend %mobile-list-icon-arrow-button;
  }
  &__icon-arrow {
    @extend %mobile-list-icon-arrow;
  }
  &__date {
    @include text($H100, 400, $N6);
    margin-right: 10px;
  }
  &__time {
    @include text($H100, 400, $N9);
  }
  &__value {
    @extend %mobile-list-value;
  }
}
</style>


