/* eslint-disable */
// Becouse 'no-shadow' and 'no-param-reassing' errors of state aren't errors
const defaultError = 'Try Again Later. Something happened.';
const defaultSuccess = 'Success';
const defaultInfo = 'Try Again Later.';
const defaultLoading = 'Loading';

const state = {
  isMessage: {
    error: false,
    success: false,
    info: false,
    loading: false,
  },
  message: {
    error: '',
    success: '',
    info: '',
    loading: '',
  },
  timer: null,
  fake: {
    dashboard: false,
    portfolio: false,
    clients: false,
    exchanges: false,
    strategies: false,
    transactions: false,
  }
};

const mutations = {
  setErrorMessage(state, msg) {
    state.message.error = msg;
  },
  setInfoMessage(state, msg) {
    state.message.info = msg;
  },
  setSuccessMessage(state, msg) {
    state.message.success = msg;
  },
  setLoadingMessage(state, msg) {
    state.message.loading = msg;
  },
  showMessage(state, type) {
    state.isMessage[type] = true;
    setTimeout(() => {
      state.isMessage[type] = false;
    }, 3000);
  },
  showLoading(state) {
    state.isMessage.loading = true;
  },
  hideLoading(state) {
    setTimeout(() => {
      state.isMessage.loading = false;
    }, 1000);
  },
  closeMessage(state) {
    Object.keys(state.isMessage).forEach(key => {
      if (key !== 'loading') {
        state.isMessage[key] = false;
      }
    })
  },
  setFake(state, type) {
    state.fake[type] = true;
  }
};

const actions = {
  closeMessage({ commit }) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        commit('closeMessage');
        resolve();
      }, 500);
    })
  },
  async showErrorMessage({ commit, dispatch }, msg) {
    const newMessage = msg ? msg : defaultError;
    commit('setErrorMessage', newMessage);
    commit('hideLoading');
    await dispatch('closeMessage');
    setTimeout(() => commit('showMessage', 'error'), 15);
  },
  async showSuccessMessage({ commit, dispatch }, msg) {
    const newMessage = msg ? msg : defaultSuccess;
    commit('setSuccessMessage', newMessage);
    commit('hideLoading');
    await dispatch('closeMessage');
    setTimeout(() => commit('showMessage', 'success'), 15);
  },
  async showInfoMessage({ commit, dispatch }, msg) {
    const newMessage = msg ? msg : defaultInfo;
    commit('setInfoMessage', newMessage);
    commit('hideLoading');
    await dispatch('closeMessage');
    setTimeout(() => commit('showMessage', 'info'), 15);
  },
  async error({ commit, dispatch }, msg) {
    const newMessage = msg ? msg : defaultError;
    commit('setErrorMessage', newMessage);
    commit('hideLoading');
    await dispatch('closeMessage');
    setTimeout(() => commit('showMessage', 'error'), 15);
  },
  async success({ commit, dispatch }, msg) {
    const newMessage = msg ? msg : defaultSuccess;
    commit('setSuccessMessage', newMessage);
    commit('hideLoading');
    await dispatch('closeMessage');
    setTimeout(() => commit('showMessage', 'success'), 15);
  },
  async info({ commit, dispatch }, msg) {
    const newMessage = msg ? msg : defaultInfo;
    commit('setInfoMessage', newMessage);
    commit('hideLoading');
    await dispatch('closeMessage');
    setTimeout(() => commit('showMessage', 'info'), 15);
  },
  async loading({ commit, dispatch }, msg) {
    const newMessage = msg ? msg : defaultLoading;
    commit('setLoadingMessage', newMessage);
    // await dispatch('closeMessage');
    setTimeout(() => commit('showLoading'), 15);
  },
  async showLoading({ commit, dispatch }, msg) {
    const newMessage = msg ? msg : defaultLoading;
    commit('setLoadingMessage', newMessage);
    // await dispatch('closeMessage');
    setTimeout(() => commit('showLoading'), 15);
  },
  async hideLoading({ commit, dispatch }) {
    commit('hideLoading');
    // await dispatch('closeMessage');
  },
  async fakeLoading({ commit, dispatch }, type) {
    commit('setFake', type);
    commit('setLoadingMessage', defaultLoading);
    setTimeout(() => {
      commit('showLoading')
      setTimeout(() => commit('hideLoading'), 300)
    }, 15);
  },
};

export default {
  namespaced: true,
  state,
  actions,
  mutations,
};
