import moment from 'moment-timezone';

const saasChart = {
  methods: {
    updateChart(timeline) {
      switch (timeline) {
        case 'week':
          this.Chart.options = Object.assign({}, this.Chart.options, {
            xaxis: {
              min: moment().subtract(1, 'week').unix(),
              max: moment().unix(),
            }
          })
          break;
        case 'month':
          this.Chart.options = {
            xaxis: {
              min: moment().subtract(1, 'month').unix(),
              max: moment().unix(),
            }
          }
          break;
        case 'year':
          this.Chart.options = Object.assign({}, this.Chart.options, {
            xaxis: {
              min: moment().subtract(1, 'year').unix(),
              max: moment().unix(),
            }
          })
          break;
        case 'all':
          this.Chart.options = {
            xaxis: {
              min: undefined,
              max: undefined,
            }
          }
          break;
        default:
      }
    }
  }
}

export default saasChart;