<template>
<div class="chart-label">
  <span class="chart-label__name">{{ name }}<slot/></span>
  <span class="chart-label__value">{{ val | numeral('0,0.[000000]') }}</span>
</div>
</template>
<script>
export default {
  name: 'ChartLabel',
  props: {
    name: {
      type: String,
      default: '',
    },
    val: {
      type: [String, Number],
      default: 0,
    }
  }
}
</script>
<style lang="scss">
.chart-label {
  &__name {
    @include text($H300, 400, $N0);
    letter-spacing: 0.5px;
    margin-right: 8px;
  }
  &__value {
    @include text($H800, 400, $G1);
    @media screen and (min-width: $screen-tablet) {
      font-size: $H500;
    }
  }
}
</style>

