<template>
  <div id="app">

    <dir class='app__messages'>
      <transition-group name="list-complete" tag="div">
        <message-loading class="list-complete-item" v-if='isMessage.loading' key='loading'/>
        <flag-message-error class="list-complete-item" v-if='isMessage.error' key='error'/>
        <flag-message-info class="list-complete-item" v-if='isMessage.info' key='info'/>
        <flag-message-success class="list-complete-item" v-if='isMessage.success' key='success'/>
      </transition-group>
    </dir>

    <transition
      name="custom-classes-transition"
      enter-active-class="animated dur02 fadeIn"
      leave-active-class="animated dur02 fadeOut"
      appear
      mode="out-in"
    >
      <router-view/>
    </transition>
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex';

export default {
  name: 'App',
  computed: {
    ...mapState({
      isMessage: state => state.messages.isMessage
    })
  },
};
</script>

<style lang='scss'>
.app__messages {
  pointer-events: none;
  position: fixed;
  top: -1px;
  right: -1px;
  left: -1px;
  z-index: 100;
  @include flex-col(flex-start, stretch);
  @media screen and (min-width: $screen-tablet) {
    align-items: center;
    top: 36px;
  }
}
</style>
