<template>
  <div class="investor-strategy-card">
    <header class="investor-strategy-card__header">
      <div class="investor-strategy-card__row">
        <avatar
          :username="crypto || label"
          :size='38'
          class="investor-strategy-card__logo"
          src=''/>
        <h2 class="investor-strategy-card__name">{{ label }}</h2>
      </div>
      <subtle-icon
        @click.native='$emit("open", id)'
        class='investor-strategy-card__button-header'
        label='Details'>
        <icon-long-arrow-right class='icon-button-right icon-small'/>
      </subtle-icon>
    </header>
    <div class="investor-strategy-card__main">
      <slot name='main'/>
    </div>
    <ul class="investor-strategy-card__list">
      <slot name='list'/>
    </ul>
    <footer class="investor-strategy-card__footer">
      <subtle-icon
        label='Details'
        @click.native='$emit("open", id)'>
        <icon-long-arrow-right class='icon-button-right icon-small'/>
      </subtle-icon>
    </footer>
  </div>
</template>
<script>
import IconLongArrowRight from '@/components/common/icons/IconLongArrowRight.vue';
import Avatar from 'vue-avatar';
import { randomBytes } from 'crypto';

export default {
  name: 'InvestorStrategyCard',
  props: {
    label: {
      type: String,
      default: '',
    },
    logo: {
      type: [String, Object],
      default: '',
    },
    crypto: {
      type: String,
      default: '',
    },
    id: {
      type: Number,
      default: null,
      required: true,
    },
  },
  components: {
    IconLongArrowRight,
    Avatar,
  },
}
</script>
<style lang="scss">
$investor-strategy-card: investor-strategy-card;

.#{$investor-strategy-card} {
  .label {
    @include text($H200, 400, $N6);
    margin-right: 5px;
  }
  .value {
    @include text($H300, 500);
  }
}

.#{$investor-strategy-card} {
  background: $N12;
  border: 1px solid $N10;
  box-shadow: 0 8px 10px 0 rgba(10,11,12,0.16);
  border-radius: 2px;
  padding: 24px;
  width: 100%;
  margin-bottom: 20px;
  @include media {
    background-color: $N13;
    flex: 0 0 49%;
    margin-right: 2%;
    &:nth-child(2n+2) {
      margin-right: 0;
    }
  }
  @include media($screen-netbook) {
    flex: 0 0 32.4%;
    margin-right: 1.3%;
    &:nth-child(2n+2) {
      margin-right: 1.3%;
    }
    &:nth-child(3n+3) {
      margin-right: 0;
    }
  }
  &__header {
    @include flex-row(space-between, center);
  }
  &__row {
    @include flex-row(flex-start, center);
  }
  &__name {
    @include text($H570, 600);
  }
  &__main {
    @include text($H300, 500);
    line-height: 1.4;
    padding: 12px 0 16px 0;
    border-bottom: 1px solid $N10;
    margin-bottom: 15px;
  }
  &__logo {
    margin-right: 16px;
  }
  &__list {
    list-style: none;
    li {
      margin-bottom: 6px;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
  &__footer {
    @include flex-row(flex-end, center);
    padding-top: 20px;
    @include media {
     display: none;
    }
  }
  &__button-header {
    @include isDesktop(flex);
    @include media {
      margin-top: 8px;
    }
  }
}
</style>

