<template>
  <nav class="inline-navigation">
    <ul class="inline-navigation__list">
      <li class="inline-navigation__item" v-for='(link, index) in links' :key='link+index'>
        <a
          href="#"
          @click.prevent='onOpen(index+1)'
          class="inline-navigation__link"
          :class='{"active": opened === index+1}'
          >{{ link }}
        </a>
      </li>
    </ul>
  </nav>
</template>
<script>
export default {
  name: 'InlineNavigation',
  props: {
    links: Array,
    opened: Number,
  },
  methods: {
    onOpen(name) {
      this.$emit('onOpen', name);
    },
  }
}
</script>
<style lang="scss">
$inline-navigation: inline-navigation;
.#{$inline-navigation} {
  @extend %inline-navigation;
  &__list {
    @extend %inline-nav-list;
  }
  &__item {
    @extend %inline-navigation-item;
  }
  &__link {
    @extend %inline-navigation-link;
  }
}
</style>
