
import { portfolio } from '@/helpers/portfolio';

const state = {
  portfolioData: [...portfolio],
};

const mutations = {
  clearFullPortfolio( state ){
    state.portfolioData = [];

  },
  setPortfolioData( state, portfolio ){ 
    state.portfolioData = [...portfolio]; 
  },
  updateAsset(state, {data, id}) {
    const idx = state.portfolioData.findIndex((el) => el.id === id);
    const subIdx = state.portfolioData[idx].assets.findIndex((el) => el.id === data.id);
    state.portfolioData[idx].assets[subIdx] = Object.assign({}, data);
  },
  removeAsset(state, {assetId, portfolioId}) {
    const idx = state.portfolioData.findIndex((el) => el.id === portfolioId);
    const subIdx = state.portfolioData[idx].assets.findIndex((el) => el.id === assetId);
    state.portfolioData[idx].assets.splice(subIdx, 1);
  },
  addAsset(state, data) {
    state.portfolioData.unshift(data);
  },
};

const getters = {
  
};

const actions = {
  updateAsset({ state, dispatch, commit }, data) {
    return new Promise((resolve, reject) => {
      commit('updateAsset', data);
      setTimeout(() => {
        dispatch('messages/success', 'Asset saved successfully', { root: true });
        setTimeout(() => {
          resolve();
        }, 500) 
      }, 500);
    })
  },
  deleteAsset({ state, dispatch, commit }, data) {
    return new Promise((resolve, reject) => {
      commit('removeAsset', data);
      setTimeout(() => {
        dispatch('messages/success', 'Asset removed successfully', { root: true });
        setTimeout(() => {
          resolve();
        }, 500) 
      }, 500); 
    })
  },
  addLocalAsset({ state, dispatch, commit }, data) {
    return new Promise((resolve, reject) => {
      commit('addAsset', data);
      
      setTimeout(() => {
        dispatch('messages/success', 'Asset added successfully', { root: true });
        setTimeout(() => {
          resolve();
        }, 500) 
      }, 500);
      
    })
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
