<template>
  <div class='investor-dashboard-details-history'>
    <h3 class='investor-dashboard-details-history__title'>History</h3>
    <inline-navigation
      :links='links'
      :opened='opened'
      @onOpen='(id) => {opened = id}'
      class="investor-dashboard-details-history__nav"/>
    <div class='desktop-none'>
      <transition
        name="custom-classes-transition"
        enter-active-class="animated dur02 fadeIn"
        leave-active-class="animated dur02 fadeOut"
        appear
        mode="out-in"
      >
      <div class='investor-dashboard-details-history__list' v-if='opened === 1' key='payout'>
          <investor-dashboard-history-item
            @closeDrop='closePayouts'
            ref='payout'
            v-for='n in 6'
            :id='"b"+n'
            :key='"a"+n'/>
      </div>
      <div class='investor-dashboard-details-history__list' v-else key='deposit'>
        <investor-dashboard-history-item
          @closeDrop='closeDeposits'
          ref='deposit'
          :is-deposit='true'
          v-for='n in 3'
          :id='"b"+n'
          :key='"b"+n'/>
      </div>
      </transition>
    </div>
    <div class="tablet-block">
      <transition
        name="custom-classes-transition"
        enter-active-class="animated dur02 fadeIn"
        leave-active-class="animated dur02 fadeOut"
        appear
        mode="out-in"
      >
        <div class='investor-dashboard-details-history__list' v-if='opened === 1' key='payout-table'>
          <investor-dashboard-table/>
        </div>
        <div class='investor-dashboard-details-history__list' v-else key='deposit-table'>
          <investor-dashboard-table :is-deposit='true' />
        </div>
      </transition>
    </div>
    
  </div>
</template>
<script>
import InvestorDashboardHistoryItem from '@/components/investor/dashboard/details/InvestorDashboardHistoryItem.vue';
import InvestorDashboardTable from '@/components/investor/dashboard/details/InvestorDashboardTable.vue';


export default {
  name: 'InvestorDashboardDetailsHistory',
  components: {
    InvestorDashboardHistoryItem,
    InvestorDashboardTable,
  },
  data() {
    return {
      links: ['Payouts', 'Deposits'],
      opened: 1,
    }
  },
  methods: {
    closeDeposits(id) {
      this.$refs.deposit.forEach(el => el.closeDrop(id));
    },
    closePayouts(id) {
      this.$refs.payout.forEach(el => el.closeDrop(id));
    },
  }
}
</script>
<style lang="scss">
$styles: investor-dashboard-details-history;

.#{$styles} {
  width: 100%;
  @include media {
    padding-top: 28px;
  }
  &__nav {
    @include media {
      margin-bottom: 16px;
    }
  }
  &__title {
    display: none;
    @include media {
      display: inline-block;
      margin-bottom: 15px;
      font-size: $H700;
      font-weight: 500;
    }
  }
}
</style>

