<template>
<table class='investor-dashboard-table'>
  <thead>
    <tr>
      <th>
        <button
          @click='onSortDate("date")'
          class='investor-dashboard-table__sort-button'>
          <span class='investor-dashboard-table__head-text'>Sent date</span>
          <icon-triangle
            :class='{"rotate-180": sortType.date }'
            class='investor-dashboard-table__icon-sort'/>
        </button>
      </th>
      <th>
        <button
          @click='onSortNumbers("currency")'
          class='investor-dashboard-table__sort-button'>
          <span class='investor-dashboard-table__head-text'>Currency</span>
          <icon-triangle
            :class='{"rotate-180": sortType.currency }'
            class='investor-dashboard-table__icon-sort'/>
        </button>
      </th>
      <th>
        <button
          @click='onSortChars("address")'
          class='investor-dashboard-table__sort-button'>
          <span class='investor-dashboard-table__head-text'>{{ isDeposit?'Recieved on':'Payout address' }}</span>
          <icon-triangle
            :class='{"rotate-180": sortType.address }'
            class='investor-dashboard-table__icon-sort'/>
        </button>
      </th>
      <th>
        <button
          @click='onSortNumbers("amount")'
          class='investor-dashboard-table__sort-button'>
          <span class='investor-dashboard-table__head-text'>Amount</span>
          <icon-triangle
            :class='{"rotate-180": sortType.amount }'
            class='investor-dashboard-table__icon-sort'/>
        </button>
      </th>
      <th>
        <button
          @click='onSortNumbers("period")'
          class='investor-dashboard-table__sort-button'>
          <span class='investor-dashboard-table__head-text'>Lockup period</span>
          <icon-triangle
            :class='{"rotate-180": sortType.period }'
            class='investor-dashboard-table__icon-sort'/>
        </button>
      </th>
      <th>
        <button
          @click='onSortChars("status")'
          class='investor-dashboard-table__sort-button'>
          <span class='investor-dashboard-table__head-text'>Status</span>
          <icon-triangle
            :class='{"rotate-180": sortType.status }'
            class='investor-dashboard-table__icon-sort'/>
        </button>
      </th>    
    </tr>
  </thead>
  <tbody>
    <tr v-for="n in 12" :key="n">
      <td >
          <p class="investor-dashboard-table__date">24/01/2019</p>
          <p class="investor-dashboard-table__time">11:23 AM</p>
      </td>
      <td>
        <cryptoicon
          class="investor-dashboard-history-item-drop__crypto-icon"
          symbol="btc"
          size="16"/> 
        <span>Bitcoin</span></td>
      <td>
        <icon-wallet class="investor-dashboard-table__value-icon"/>
        <span>43fs2434f234sdf33</span>
      </td>
      <td class='investor-dashboard-table__value'><span>3,987,00 $</span></td>
      <td><span>235</span></td>
      <td :class='{"rejected": status === "rejected", "successful": status === "successful"}'><span>Rejected</span></td>
    </tr>
  </tbody>
</table>
</template>

<script>
import IconWallet from '@/components/common/icons/IconWallet.vue';
import IconTriangle from '@/components/common/icons/IconTriangle.vue';
import IconDots from '@/components/common/icons/IconDots.vue';
import moment from 'moment';

export default {
  name: 'InvestorDashboardTable',
  props: {
    isDeposit: {
      type: Boolean,
      default: false,
    },
    status: {
      type: String,
      default: 'rejected',
    },
  },
  components: {
    IconTriangle,
    IconDots,
    IconWallet,
  },
  data() {
    return {
      sortType: {
        data: false,
        currency: false,
        address: false,
        amount: false,
        period: false,
        status: false,
      },
      sortNumers: true,
    }
  },
  methods: {
    async onSortNumbers(type) {
      this.onToggleSort(type);
      await this.$nextTick();
      if (this.sortType[type]) {
        this.table.sort((a, b) => Number(a[type]) - Number(b[type]));
      } else {
        this.table.reverse();
      }
      
    },
    async onSortDate(type) {
      this.onToggleSort(type);
      await this.$nextTick();
      if (this.sortType[type]) {
        this.table.sort((a, b) => moment(a[type], 'DD/MM/YYYY').diff(moment(b[type], 'DD/MM/YYYY')));
      } else {
        this.table.reverse();
      }
      
    },
    async onSortChars(type) {
      this.onToggleSort(type);
      await this.$nextTick();
      if (this.sortType[type]) {
        this.table.sort((a, b) => {
          if (a[type] > b[type]) {
            return 1;
          }
          if (a[type] < b[type]) {
            return -1;
          }
          return 0;
        });
      } else {
        this.table.reverse();
      }
    },
    onToggleSort(type) {
      Object.keys(this.sortType).forEach(key => {
        key === type? this.sortType[key] = !this.sortType[key] : this.sortType[key] = false;
      })
    }
  },
};
</script>

<style lang="scss">
@import '~@/scss/components/table';
$styles: investor-dashboard-table;
.#{$styles} {
  @extend %saas-table;
  .rejected {
    color: $Y1;
  }
  .successful {
    color: $G1;
  }
  thead {
    background-color: transparent;
  }
  tbody {
    max-height: auto;
    overflow-y: visible;
  }
  td {
    & > * {
      vertical-align: middle;
    }
    padding: {
      top: 16px !important;
      bottom: 16px !important;
    };
    border-bottom: 1px solid $N10;
  }
  td:last-child,
  th:last-child {
    text-align: left;
    width: 120px;
  }
  &__sort-button {
    @include text($H200, 500, $N6);
    fill: $N6;
    @extend %text-transition;
    &:hover {
      color: $N4;
      fill: $N4;
    }
    &:active {
      color: $N6;
      fill: $N6;
    }
  }
  &__head-text {
    margin-right: 8px;
  }
  &__icon-sort {
    @extend %icon-transition;
    fill: inherit;
    width: 8px;
    height: 8px;
  }
  td.#{$styles}__value {
    color: $G1;
    font-weight: 500;
  }
  &__time {
    color: $N6;
  }
  &__col {
    @include flex-col;
  }
  &__date {
    margin-bottom: 6px;
  }
  &__value-icon {
    $size: 20px;
    width: $size;
    height: $size;
    margin-right: 6px;
    fill: $N0;
  }
  &__crypto-icon { 
    margin-right: 6px;
  }
}
</style>

